@keyframes arrow-rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

.stopwatch__minutes .speed-up {
  transform-origin: bottom;
  animation: 600s steps(60, end) infinite arrow-rotate;
}

.stopwatch__minutes-arrow {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite arrow-rotate;
}

.stopwatch__seconds {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

.stopwatch__seconds .speed-up {
  transform-origin: bottom;
  animation: 10s linear infinite arrow-rotate;
}

.stopwatch__seconds-arrow {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite arrow-rotate;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
}
